import React , { Component } from 'react';
import { connect } from 'react-redux';
import { browserHistory  } from 'react-router';
import { Form, Input, DatePicker, Col , Button ,Cascader  ,Row ,InputNumber,Select } from 'antd';
import { getCurrent } from '../index/action'
import {Title} from '../../../public/title/title';

import {getProvinceAjax ,getCityAjax ,codeAjax ,verfiyCodeAjax ,saveForm} from './action';

const FormItem = Form.Item;
const InputGroup = Input.Group;

@connect(state =>{
    const {city , province ,verfiy,formData} = state.fillStoreRs;
    return {
        city ,
        province ,
        verfiy,
        formData
    }
},dispatch =>({
    setCurrent:(current) => dispatch(getCurrent(current)),
    ProvinceAjax:(url) => dispatch(getProvinceAjax(url)),
    CityAjax:(url) => dispatch(getCityAjax(url)),
    getCode:(url) => dispatch(codeAjax(url)),
    getVerfiy:(url) => dispatch(verfiyCodeAjax(url)),
    setForm:(data) => dispatch(saveForm(data))
}))
export default class FillStore extends Component{
    constructor(props){
        super(props);

        this.handleSubmit = this.handleSubmit.bind(this);
        this.onPhoneRq = this.onPhoneRq.bind(this);
        this.onGetCode = this.onGetCode.bind(this);
        this.onProvince = this.onProvince.bind(this);
    }
    componentWillMount(){
        this.props.setCurrent(1);
        this.props.ProvinceAjax({url:'/api/seller/apply/provinceList',method:'POST'});
    }
    componentDidMount(){
        const form = this.props.form;
        form.setFieldsValue(this.props.formData);
    }
    handleSubmit(e){
        let isForm = true;
        const form = this.props.form;
        e.preventDefault();
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
            }else{
                isForm = false;
            }
        });
        console.log(form.getFieldsValue());
        if(isForm){
            this.props.setForm(Object.assign(form.getFieldsValue(),{showModel:true}));
            this.props.getVerfiy({url:'/api/seller/apply/checkVerifyCode',method:'POST',data:{mobileNo:form.getFieldValue('personPhone'),verifyCode:form.getFieldValue('verifyCode')}});
        }
    }
    onPhoneRq(rule, value, callback){
        if(/^\d*$/g.test(value)){
            if(value.length != 11){
                callback('11!');
            }
        }else{
            callback('!');
        }
    }
    onGetCode(){
        const form = this.props.form;
        if(form.getFieldValue('personPhone')){
            if(form.getFieldValue('personPhone').length == 11){
                this.props.getCode({url:'/api/seller/apply/sendVerifyCode',method:'POST',data:{mobileNo:form.getFieldValue('personPhone')}});
            }
        }
    }
    onProvince(val){
        const form = this.props.form;
        this.props.CityAjax({url:'/api/seller/apply/cityList',method:'POST',data:{provinceCode:val}});
        form.setFieldsValue({companyCity:''});
    }
    render(){
        const formItemLayout  = {
            labelCol : { span: 6 },
            wrapperCol : { span: 12 }
        };
        const tailFormItemLayout = {
            wrapperCol: {
                span: 14,
                offset: 6
            }
        };
        const { getFieldDecorator } = this.props.form;

        return (
            <div className="store-box">
                <Title title="" />
                <Form horizontal onSubmit = {this.handleSubmit}>
                    <FormItem
                        {...formItemLayout}
                        label=""
                        hasFeedback>
                        {getFieldDecorator('company', {
                            rules: [{ required: true, message: '!' }]
                        })(
                            <Input placeholder="" />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="">
                        <Row gutter={24}>
                            <Col span={12}>
                                {getFieldDecorator('companyProvince')(
                                    <Select placeholder="" onChange={this.onProvince}>
                                        {this.props.province.info.map((d) =>
                                                <Option value={d.code}>{d.name}</Option>
                                        )}
                                    </Select>
                                )}
                            </Col>
                            <Col span={12}>
                                {getFieldDecorator('companyCity')(
                                    <Select placeholder="">
                                        {this.props.city.success ? this.props.city.info.map((d) =>
                                                <Option value={d.code}>{d.name}</Option>
                                        ) : null}
                                    </Select>
                                )}
                            </Col>
                        </Row>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="">
                        {getFieldDecorator('companyAdd',{initialValue:''})(
                            <Input placeholder="" />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="">
                        {getFieldDecorator('fax',{initialValue:''})(
                            <Input placeholder="" />
                        )}
                    </FormItem>
                    <Title title="" />
                    <FormItem
                        {...formItemLayout}
                        label=""
                        hasFeedback>
                        {getFieldDecorator('contactName',{
                            rules:[{required:true,message:''}]
                        })(
                            <Input placeholder="" />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label=""
                        >
                        {getFieldDecorator('personPhone',{
                            rules:[
                                {required:true,message:''},
                                {validator: this.onPhoneRq}
                            ]
                        })(
                            <Input placeholder="" />
                        )}
                    </FormItem>
                    <FormItem
                       labelCol={{ span: 6 }}
                        wrapperCol= {{ span: 12 }}
                        label=""
                        >
                        {getFieldDecorator('verifyCode',{
                            rules:[
                                {required:true,message:''}
                            ]
                        })(
                            <div className="yzm-box">
                                <Row gutter={16}>
                                    <Col span={10}><Input  placeholder="" /></Col>
                                    <Col span={6}> <Button  className="btnStore" onClick={this.onGetCode}></Button></Col>
                                </Row>
                            </div>
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label=""
                        hasFeedback>
                        {getFieldDecorator('email',{
                            rules:[
                                {required:true,message:''},
                                {type:'email',message:''}
                            ]
                        })(
                            <Input  placeholder=" " />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="QQ"
                        hasFeedback>
                        {getFieldDecorator('qq',{
                            rules:[
                                {required:true,message:''}
                            ]
                        })(
                            <Input  placeholder="QQ" />
                        )}
                    </FormItem>

                    <Title title="" />
                    <FormItem
                        {...formItemLayout}
                        label=""
                        hasFeedback>
                        {getFieldDecorator('bankUser',{
                            rules:[
                                {required:true,message:''}
                            ]
                        })(
                            <Input  placeholder=" " />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label=""
                        hasFeedback>
                        {getFieldDecorator('bankCode',{
                            rules:[
                                {required:true,message:''}
                            ]
                        })(
                            <Input maxlength='10'  placeholder="" />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label=""
                        hasFeedback>
                        {getFieldDecorator('bankNameBranch',{
                            rules:[
                                {required:true,message:''}
                            ]
                        })(
                            <Input  placeholder="" />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label=""
                        hasFeedback>
                        {getFieldDecorator('brandNameCode',{
                            initialValue:''
                        })(
                            <Input  placeholder="" />
                        )}
                    </FormItem>

                    <FormItem {...tailFormItemLayout}>
                        <Button type="primary" htmlType="submit" size="large"></Button>
                    </FormItem>
                </Form>
            </div>
        )
    }
}

FillStore = Form.create({})(FillStore);


